
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {mapState,mapGetters,mapMutations} from "vuex";
import VmMutipleBackTop from "vue-multiple-back-top";

export default {
name: 'app',
components:{
Tab,
TabItem,
ButtonTab,
ButtonTabItem,
XHeader,
Actionsheet ,
VmMutipleBackTop,
Badge
},
data(){
return{
badgeText:"",
};
},

created(){

var loginStatus=window.localStorage.getItem("loginStatus");
if(loginStatus){

this.mutationLogin();

this.mutationLoginname(window.localStorage.getItem("loginname"));
this.mutationAccesstoken(window.localStorage.getItem("accesstoken"))
this.mutationsAuthorId(window.localStorage.getItem("author_id"))
}
if(this.isLogined){

this.getUnreadMagCount();
var that = this;
window.setInterval(function(){
that.getUnreadMagCount();
},1000*60*5);
}
},
computed:{
...mapGetters(['moreMenus']),
...mapState(["topics","unLoginedMoreMenus","loginedMoreMenus","isLogined",]),



isShowMoreMenusV:{
get(){

return this.$store.state.isShowMoreMenus;
},
set:function(nval){

this.$store.commit("mutationIsShowMoreMenus",nval)
}
}

},
methods:{
...mapMutations(['mutationLogin','mutationLoginname','mutationAccesstoken','mutationsAuthorId']),

getUnreadMagCount:function(){
var that = this;
this.$http.get("/message/count",{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
console.log(res.data);
if(res.data && res.data.success){
that.badgeText = res.data.data;
}
})
},


showMoreMenus:function(){



this.isShowMoreMenusV = true;
},

handleClickMoreMenu:function(menuKey, menuItem){
console.log(menuKey);
console.log(menuItem);

this.$router.push("/"+menuKey)
},

routetohome:function(){
this.$router.replace("/")
}
},
}



