import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations"
import actions from "./actions"

//全局注册
Vue.use(Vuex);

//创建store对象
const store = new Vuex.Store({
	//状态数据
	state:{
		//主题分类数据
			topics:[
  			{name:"all",desc:"全部"},
  			{name:"good",desc:"精华"},
  			{name:"share",desc:"分享"},
  			{name:"ask",desc:"问答"},
  			{name:"job",desc:"招聘"},
  			{name:"dev",desc:"测试"},
  		],
  		unLoginedMoreMenus:{ //未登录状态
  			login:"登录"
  		},
  		loginedMoreMenus:{  //已登录状态
  			create:"发表新话题",
  			center:"个人中心",
  			msg:"消息通知",
  			logout:"退出"
  		},
  		isLogined:false,  //表示登录状态
  		loginname:'',     //登陆用户名
  		accesstoken:'',	  //登录用户的accesstoken
  		author_id:"",     //表示登录用户的id
  		isShowMoreMenus:false  //是否显示actionsheet
  },
  	
	
	//状态数据的计算属性
	getters:{
		moreMenus:function(state){
	  		if(state.isLogined){//判断，如果为true，说明登录
	  			return state.loginedMoreMenus;//返回登录后的菜单
	  		}
	  		return state.unLoginedMoreMenus;//未登录，返回菜单
	  	},
	  	getTopicDescByTab: function(state) {
	//需要返回一个函数结构
	return function(tab) {
		for(var index = 0; index < state.topics.length; index++) {
			if(state.topics[index].name == tab) {
				return state.topics[index].desc;
			}
		}
	};

}
	},
	mutations,
	actions
	
});

export default store;






























