
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState, mapGetters } from "vuex";
export default {
name:"collect",
props:["name"],
components:{
Group,
Cell,
Scroller,
Tab,
TabItem
},
data(){
return{
lists:[],
page:1, //当前页数
limit:15, //每一页显示的主题数量
pullupConfig: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
}
};
},
computed:{
...mapGetters(["getTopicDescByTab"]),
...mapState(["topics"])
},

methods:{

routeto: function(index) {

console.log(index);






this.$router.push('/topiclist/' + this.topics[index].name);
},
loadMoreData: function() {//上拉刷新,进行ajax加载更多数据
var that =this;
this.page = this.page+1;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
for (let i = 0; i<response.data.data.length; i++) {
that.lists.push(response.data.data[i]);
}

that.$refs.pullup.donePullup();
}

})
}
},


beforeRouteEnter(to,from,next){






next(function(vm){

vm.$http.get("/topic_collect/"+to.params.name,{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response) {

if(response.data.success){
vm.lists = response.data.data;
}
})
});
},

beforeRouteUpdate:function(to,from,next){
var that = this;
this.page = 1;

this.$http.get("/topic_collect/"+to.params.name,{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
that.lists = response.data.data;
}
});

next();
},

}



