
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {mapMutations} from "vuex";
export default {
name:"login",
components: {
Group,
XInput,
XButton,
Toast
},
data(){
return{
accesstoken :'',
isShowToast:false,
toastText:""
}
},
methods:{
...mapMutations(["mutationLogin","mutationLoginname","mutationAccesstoken","mutationsAuthorId"]),

dologin:function(){
var that = this;
this.$http.post("/accesstoken",{
accesstoken:this.accesstoken
}).then(function(res){//接收后响应
console.log(res.data);

that.mutationLogin();
that.mutationLoginname(res.data.loginname);
that.mutationAccesstoken(that.accesstoken);
that.mutationsAuthorId(res.data.id);




window.localStorage.setItem("loginStatus",true);
window.localStorage.setItem("loginname",res.data.loginname);
window.localStorage.setItem("accesstoken",that.accesstoken);
window.localStorage.setItem("author_id",res.data.id);

that.$router.push("/user/"+res.data.loginname);

}).catch(function(error){

console.log(error.response);
that.isShowToast = true;
that.toastText = error.response.data.error_msg
})
}
}
}



