
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
name:"msg",
components: {
ButtonTab,
ButtonTabItem,
Group,
Cell,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
data(){
return{
selectedTabIndex:0,
hasReadMsg:null,
hasNotReadMsg:null
}
},

created:function(){

var that = this;
this.$http.get("/messages",{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
console.log(res.data);
that.hasReadMsg = res.data.data.has_read_messages;
that.hasNotReadMsg = res.data.data.hasnot_read_messages;
})
},
methods:{

markRead:function(mid){
console.log(mid)
var that = this;
this.$http.post("/message/mark_one/"+mid,{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data);
if(res.data && res.data.success){

console.log("标记已读成功！");

that.$router.go(0);


}
})
},

markAll:function(){
var that = this;
this.$http.post("/message/mark_all",{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
if(res.data && res.data.success){

that.$router.go(0);
}
})
}
},

}



